.owl-carousel.owl-theme
  .owl-nav.disabled + .owl-dots, .owl-dots
    margin-top: 2rem
  .owl-dots
    button
      span
        transition: all .6s
        width: 1rem
        height: 1rem
        background: $grey-l1
      &:hover, &.active
        span
          background: $second
      &.active span
        transform: scale(1.3)

.owl-carousel
  &.owl-carousel1, &.owl-carousel4
    .item
      border-radius: 1.5rem
      overflow: hidden
      position: relative
      img
        @include coverImage


.owl-carousel.owl-carousel1
  .owl-item
    opacity: .2
    transition: all .6s
    &.active
      opacity: 1
  .item
    padding-bottom: 70%

.owl-carousel

  &.owl-carousel2, &.owl-carousel3, &.owl-carousel5

    .owl-stage, .item
      display: flex

    .owl-stage
      align-items: center

    .item
      justify-content: center
      padding: 1rem
      img
        max-width: 100%
        width: auto
        max-height: 6rem

.owl-carousel.owl-carousel4
  @include respond(xs)
    padding: 0
  &, .owl-stage-outer
    position: relative
  .owl-stage-outer
    //z-index: 2
  .owl-nav
    display: flex
    align-items: center
    justify-content: space-between
    width: 100%
    height: 0
    opacity: 0
    @include posAbsTL(50%, 0)
    transform: translateY(-50%)
    margin: 0
    transition: all .3s
    padding: 0 1rem
    //z-index: 1
    @include respond(xs)
      display: none
    button
      &.owl-prev, &.owl-next
        width: 3.5rem
        height: 5rem
        transition: all .3s
        border-radius: 1rem
        background: rgba($dark, .7)
        display: flex
        align-items: center
        justify-content: center
        font-size: 2rem
        color: $white
        i
          display: block
          line-height: 3.5rem
        &:hover
          background: $prime
          color: $black

  &:hover .owl-nav
    opacity: 1

  .item
    padding-bottom: 75%
