.content

  img:not(.company-logo)
    filter: drop-shadow(1.6rem 1.6rem 0 lighten($prime, 30%))
    border-radius: 1.5rem
    width: 75%
    display: block
    @include respond(sm)
      width: calc(100% - 1.6rem)

  .company-logo
    max-width: 15rem
    max-height: 4rem
    width: auto

  picture, img:not(.company-logo), .owl-carousel
    &:not(:first-child)
      margin-top: 3.2rem
    &:not(:last-child)
      margin-bottom: 4.8rem

  picture
    padding-bottom: 52.5%
    width: 80%
    @include respond(sm)
      width: 100%
      padding-bottom: 56.25%
    img
      @include coverImage
      &:nth-child(n+1)
        margin: 0
        filter: none
        width: 100%

  h1, h2, h3, h4, h5, h6
    color: $dark
    text-transform: capitalize
    span.lower
      text-transform: none
    &:not(:last-child)
      margin-bottom: 2.4rem
    &:not(:first-child)
      margin-top: 2.4rem

  h3
    @extend .title-md
  h4
    @extend .title-sm

  p
    color: $grey
    @extend .text-md
    &:nth-child(n+2)
      margin-top: 1.2rem

  a
    color: $second
    font-weight: 500

  .owl-carousel .owl-dots
    display: none
    @include respond(xs)
      display: block

.contents
  &:not(:first-child)
    margin-top: 6rem
    @include respond(xs)
      margin-top: 3rem
  .content:nth-child(n+2)
    margin-top: 4rem