.dashboard
  display: flex
  gap: 4rem
  align-items: flex-start
  $this: &
  @include respond(sm)
    gap: 2rem
    margin-top: 6rem
  @include respond(xs)
    flex-wrap: wrap

  &, &__body
    &, & > *
      width: 100%

  &__sidebar
    scroll-margin: 7rem
    position: relative
    width: 25rem
    flex-shrink: 0
    background: $light1
    padding: 2.5rem
    border-radius: 1.5rem
    max-height: 100%
    overflow-y: auto
    z-index: 1
    @include respond(sm)
      width: 20rem
      padding: 1.5rem
      border-radius: 1rem
    @include respond(xs)
      width: 100%
      overflow: visible

      .menu
        @include posFix(0, auto, 0, 0)
        background: $light1
        box-shadow: $shadow2
        padding: 9rem 2rem 14rem
        display: none
        height: 100%
        z-index: 4
        overflow: hidden
        overflow-y: auto
        animation: to-right .3s

    &__cta.active + .menu
      display: block

  &__body
    flex-grow: 1
    @include respond(xs)
      margin-top: .5rem

  &__tab
    animation: fade-up .3s forwards
    scroll-margin: 7rem

    &:nth-child(n+2)
      display: none

  &__article
    display: flex
    @include respond(sm)
      flex-wrap: wrap

    &:not(.flex--middle)
      align-items: flex-start

      #{$this}__img
        margin-top: 6rem

  &__img
    width: 35%
    padding-bottom: 25%
    position: relative
    flex-shrink: 0
    margin-left: 3rem
    border-radius: 1.5rem
    overflow: hidden
    filter: drop-shadow(1rem 1rem 0 lighten($prime, 30%))
    img
      @include coverImage
      display: block

    @include respond(sm)
      margin: 2rem 0 0 !important
      width: 70%
      padding-bottom: 45%
    @include respond(xs)
      width: 100%
      padding-bottom: 60%

  &__content
    display: flex
    justify-content: center
    flex-direction: column
    order: -1
    flex-grow: 1