.menu
  $this: &

  &__item:nth-child(n+2)
    margin-top: 1.5rem
    @include respond(xs)
      margin-top: 2rem

  &__title
    font-size: 1.6rem
    font-weight: 500
    display: block
    @include respond(xs)
      font-size: 2rem

  &__list
    position: relative
    z-index: 1
    margin-top: .5rem
    @include respond(xs)
      margin-top: 1rem
    li
      padding: .5rem 1rem
      cursor: pointer
      @include respond(sm)
        padding: .5rem 0
      &, a
        font-size: 1.5rem
        transition: color .3s
        @include respond(sm)
          font-size: 1.4rem
        @include respond(xs)
          font-size: 1.7rem
      &:hover
        color: $second
      &.active
        color: $second
        font-weight: 500
        padding-right: .3rem