@keyframes fade-up
  0%
    transform: translateY(.5rem)
    opacity: 0
  100%
    transform: translateY(0)
    opacity: 1

@keyframes fade-up-big
  0%
    transform: translateY(2rem)
    opacity: 0
  100%
    transform: translateY(0)
    opacity: 1

@keyframes to-right
  0%
    transform: translateX(-2rem)
  100%
    transform: translateY(0)

@keyframes to-right-big
  0%
    transform: translateX(-100%)
  100%
    transform: translateY(0)

@keyframes fade-down
  0%
    transform: translateY(-.5rem)
    opacity: 0
  100%
    transform: translateY(0)
    opacity: 1

@keyframes fade-in
  from
    opacity: 0
  to
    opacity: 1

//--- Only for typed cursor

@keyframes set-width
  from
    width: 0
  to
    width: 2px

//--- End

@keyframes drag
  from
    transform: translateY(-.5rem)
  20%
    transform: translateY(.5rem)
  40%
    transform: translateY(-.5rem)
  60%
    transform: translateY(.5rem)
  80%
    transform: translateY(-.5rem)
  to
    transform: translateY(.5rem)

@keyframes dragColorsSecond
  from
    background: $second
  66%
    background: darken($second, 10%)
  to
    background: $second
