.btn
  display: inline-flex
  align-items: center
  justify-content: center
  padding: 1.6rem 3.2rem
  font-weight: 600
  font-size: 1.6rem
  outline: none
  box-shadow: none
  border: none
  cursor: pointer
  transition: all .4s
  border-radius: 1.5rem
  backface-visibility: hidden
  text-align: center
  i
    margin-left: .5rem
    font-style: normal
  &:not(.inline-labels__trigger)
    &:hover, &:focus
      transform: translateY(-.5rem)
    &:active
      transform: translateY(-.2rem)
      box-shadow: none

  @include respond(xs)
    padding: 1.6rem 2rem

  &--dark, &--blue, &--red,
  &--second
    color: $white

  &--prime
    background: $prime
    color: $dark
    &:hover, &:focus
      background: lighten($prime, 10%)
      box-shadow: 0 5px 10px rgba($prime, 0.4)

  &--second
    background: $second
    &:hover, &:focus
      background: lighten($second, 10%)
      box-shadow: 0 5px 10px rgba($second, 0.4)

  &--second-out
    color: $second
    border: 1px solid $second
    background: $white
    &:hover, &:focus
      background: $second
      color: $white

  &--dark
    background: $dark
    &:hover, &:focus
      box-shadow: 0 5px 10px rgba($dark-l1, 0.4)
      background: $black

  &--blue
    background: $blue
    &:hover, &:focus
      background: $blue-l1
      box-shadow: 0 5px 10px rgba($blue, 0.4)

  &--red
    background: $red
    &:hover, &:focus
      background: $red-l1
      box-shadow: 0 5px 10px rgba($red, 0.4)

  &--yellow-l3
    background: $yellow-l3
    color: $dark
    &:hover, &:focus
      background: $yellow-l1
      color: $dark

  &--big
    font-size: 1.8rem
    padding: 1.8rem 3.8rem
    i
      margin-left: .8rem

  &--sm
    padding: 1.4rem 2rem
    font-size: 1.4rem
    font-weight: 500

  &--full
    width: 100%

  //.btn--rich
  &--rich
    background: linear-gradient(-245deg, $purple-l2 0%, $blue-l1 100%)
    color: $white
    &:hover
      box-shadow: 0 0 5rem rgba($blue-l1, 60%)


.inline-btns
  display: inline-flex
  flex-wrap: wrap
  gap: 2rem
  @include respond(sm)
    display: flex
    .btn
      flex-grow: 1
  @include respond(xs)
    gap: 2rem

  &--equal
    display: flex
    .btn
      width: calc(50% - 3rem)
      padding-left: 0
      padding-right: 0
      &.btn--big
        @include respond(xs)
          width: 100%

.scroll-up
  width: 5.5rem
  height: 5.5rem
  border-radius: 50%
  background: lighten($second, 45%)
  transition: all .5s
  @include posFixBR(4.5rem, 3rem)
  cursor: pointer
  display: none
  align-items: center
  justify-content: center
  animation: fade-up-big .4s
  z-index: 4
  @include respond(sm)
    display: none!important
  i
    color: $second
    font-size: 2.5rem
    transition: inherit
    transform: translateY(0)
  &:hover
    background: $second
    i
      color: $white
      transform: translateY(-3px)
  &.active
    display: flex