.page-header
  padding-top: 13rem
  @include respond(sm)
    padding-top: 10rem

  &__title
    line-height: 1
    max-width: 90rem
    margin: 0 auto
    text-align: center

  &__sub-nav
    margin-top: 2rem
    @include respond(sm)
      display: none

  &__footer
    display: flex
    justify-content: center
    flex-wrap: wrap
    margin-top: 2rem

  &__date
    font-style: italic
    width: 100%
    margin-top: 1.5rem