.header
  background: $white
  padding: 2.4rem 0
  box-shadow: $shadow1
  @include posFixTL(0, 0)
  width: 100%
  transition: all .3s
  z-index: 3
  @include respond(xs)
    padding: 2rem 0

  $this: &

  a:not(.btn)
    font-weight: 500
    color: $dark
    &:hover
      color: $second

  &__row
    display: flex
    align-items: center

  &__nav-btn
    width: 3.6rem
    height: 2.4rem
    cursor: pointer
    position: relative
    margin-right: 3.2rem
    z-index: 3
    @include respond(sm)
      margin-right: 2rem
    span
      @include posAbsTL(50%, 0)
      transform: translateY(-50%)
      &:before, &:after
        content: ''
      &, &:before, &:after
        display: block
        width: 3.6rem
        height: .3rem
        background: $dark
        transition: all .3s $cube1
        backface-visibility: hidden
      &:before
        @include posAbsTL(-1rem, 0)
      &:after
        @include posAbsTL(1rem, 0)

    &:hover span
      &:before
        transform: translateY(.3rem)
      &:after
        transform: translateY(-.3rem)

    &.active span
      background: transparent
      &:before
        transform: translateY(1rem) rotate(45deg)
      &:after
        transform: translateY(-1rem) rotate(-45deg)

  &__logo a
    max-width: 9rem
    max-height: 4rem
    margin-top: -.7rem
    img
      display: block
      transition: inherit
    @include respond(xs)
      max-width: 8rem
      max-height: 5rem

  &__nav
    background: none
    transition: all .4s $cube1
    @include respond(sm)
      @include posFixTL(0, 0)
      width: 100%
      height: 100%
      background: lighten(rgba($prime, .85), 10%)
      backdrop-filter: blur(15px)
      z-index: 2
      align-items: center
      justify-content: center
      display: none
      animation: to-right-big .4s
      &.active
        display: flex

  &__links
    display: flex
    align-items: center
    gap: 2rem
    margin-left: 3rem
    font-size: 1.6rem
    li a
      position: relative
      overflow: hidden
      padding-bottom: .1rem
      &:before
        content: ''
        width: 100%
        height: 2px
        background: $second
        display: block
        @include posAbsBL(0, -100%)
        transition: inherit
        @include respond(sm)
          display: none
      &:hover:before
        left: 0
    @include respond(md)
      gap: 1rem
      margin-left: 1rem
      li a:not(.btn)
        font-size: 1.4rem
    @include respond(sm)
      margin: 0
      flex-wrap: wrap
      gap: 2rem
      li
        width: 100%
        text-align: center
        a:not(.btn)
          font-size: 2rem
          //color: $white

  &__phone
    flex-grow: 1
    justify-content: flex-end
    &, a
      display: flex
    a
      align-items: center
      i
        margin-right: .5rem
        color: $second

  &__cta
    margin-left: 3rem
    @include respond(md)
      margin-left: 2rem

  &.sticky
    box-shadow: $shadow2
    padding: 1.2rem 0

    #{$this}__logo a
      max-width: 7rem
      max-height: 3rem