.list-tabs, .list-tabs-inline
  display: flex
  li
    transition: background-color .3s, color .3s, border .3s
    cursor: pointer
    padding: 1rem 1.4rem
    &.active
      font-weight: 600

.list-tabs-inline
  overflow: hidden
  gap: 1rem
  flex-wrap: wrap
  $this: &
  li
    font-size: 1.4rem
    flex-grow: 1
    text-align: center
    display: flex
    align-items: center
    justify-content: center
    min-height: 100%
    border-radius: 1rem
    border: 1px solid rgba($grey-l1, .4)
    position: relative
    overflow: hidden
    transition: color .3s
    z-index: 1
    &:before
      content: ''
      width: 0
      height: 0
      @include posAbsTL(50%, 50%)
      transform: translate(-50%, -50%)
      border-radius: 1rem
      z-index: -1
      transition: all .3s
      opacity: 0
    &:hover, &.active
      &:before
        width: 110%
        height: 110%
        opacity: 1

  &--items-4 li
    width: calc(25% - 1rem)

  &--items-5 li
    width: calc(20% - 1rem)

  &--items-6 li
    width: calc((100% / 6) - 1rem)

  @include respond(sm)
    flex-wrap: wrap
    border-radius: 0
    border: none
    gap: 0
    li
      width: 100%
      font-size: 1.8rem
      border: none
      border-radius: 0
      &:before
        display: none

  &--prime li
    &:before
      background: lighten($prime, 30%)
    &:hover, &.active
      color: $dark
    &.active
      background: $prime
      border-color: lighten($prime, 30%)

  &--second li
    &:before
      background: rgba($second, .8)
    &:hover
      @include respondAboveSM
        color: $white
    &.active
      color: $white
    &.active
      background: $second

.list-tabs
  flex-direction: column
  flex-wrap: wrap
  gap: 1.6rem
  li
    border-radius: 1.5rem
    font-size: 1.4rem
  @include respond(sm)
    flex-direction: row
    gap: .8rem 3rem
    li
      width: calc(50% - 1.6rem)
      display: flex
      align-items: center
  @include respond(xs)
    gap: .8rem
    flex-wrap: nowrap
    overflow-y: auto
    li
      width: 45%
      min-height: 58px
      flex-shrink: 0
      justify-content: center
      text-align: center
      background: darken($light1, 2%)
      padding: 1.2rem .8rem
      &.active
        padding: 1.2rem .3rem
  &--second li
    &:hover
      background: rgba($second, .1)
    &.active
      background: rgba($second, .2)
    &:hover, &.active
      color: $second

  &--prime li
    &:hover
      background: rgba($prime, .1)
    &.active
      background: rgba($prime, .2)
    &:hover, &.active
      color: $dark

.list-tabs-boxed
  display: flex
  flex-wrap: wrap
  gap: 1rem
  li
    font-size: 1.2rem
    font-weight: 600
    padding: 1rem
    background: $light1
    border-radius: 1rem
    text-align: center
    cursor: pointer
    transition: all .3s
    min-height: 100%
    display: flex
    align-items: center
    &:hover
      color: $second
    &.active
      background: $prime-l-03
      color: $dark
  @include respond(xs)
    flex-wrap: nowrap
    overflow-y: auto
    li
      flex-grow: 1
      min-height: 50px
      height: auto
      flex-shrink: 0
      justify-content: center
      text-align: center
      background: darken($light1, 2%)
      font-size: 1.6rem
      width: 45%