.hero-home
  position: relative
  overflow: hidden
  @include respond(sm)
    box-shadow: 0 1px 0 rgba($grey-l1, .5)

  &--nav-above
    margin-top: 102px
    padding-top: 6rem
    @include respond(sm)
      margin-top: 59px
      padding-top: 8rem
    @include respond(xs)
      padding-top: 7rem
      margin-top: 45px

  &__sup
    color: $second
    display: block
    margin-bottom: 3.2rem
    font-weight: 600

  &__content
    z-index: 1

  &__text,
  &__list, &__cta
    &:nth-child(n+2)
      margin-top: 3.2rem

  &__title
    &.title-lg
      line-height: 1.5
      @include respond(sm)
        line-height: 1.3


    //-- Typed plugin cursor style

    &#text-animation
      display: inline-flex
      align-items: center
      b:nth-child(n+2)
        display: none
    .typed-cursor
      display: inline-block
      background: $dark
      width: 2px
      height: 100%
      overflow: hidden
      &:not(.typed-cursor--blink)
        width: 0
        animation: set-widts .3s forwards 10s

    //-- End


  &__text:not(:last-child)
    margin-bottom: 5rem

  &__img
    picture
      height: calc(100% - 1.6rem)
    @include respond(sm)
      background: rgba($second, .06)
      @include posAbsTL(0, 0)
      width: 100%
      overflow: hidden
      z-index: -1
      &, picture
        height: 100%
      picture
        filter: none
        border-radius: 0
        @include posAbsZero
        opacity: .06
        margin-right: 0
        img
          @include coverImage

  &__logos
    p
      margin: 3.2rem 0 1rem
    @include respond(sm)
      padding: 2rem
      background: $white
      width: 100%
      position: relative
      margin-bottom: -.75rem
      margin-top: 4rem
      border-radius: 1.5rem 1.5rem 0 0
      p
        margin: 0 0 1rem
