.items-img-v
  display: flex
  flex-wrap: wrap
  margin: -1rem
  @include respond(xs)
    margin: -1rem
  &__item
    padding: 1rem
    width: calc(100% / 3)
    @include respond(sm)
      width: 50%
    @include respond(xs)
      padding: 1rem

.item-img-v
  cursor: pointer

  &, &__img
    position: relative

  &__title, &__img img
    transition: all .6s

  &__img
    padding-bottom: 56.25%
    overflow: hidden
    border-radius: 1.5rem
    img
      @include coverImage

  &__title
    color: $dark
    font-weight: 700
    text-align: center
    padding: 1rem
    font-size: 1.8rem
    @include respond(xs)
      width: 100%
      background: none
      box-shadow: none
      padding: 1rem 0 0
      font-size: 1.6rem

  &:hover &__img img
    transform: scale(1.1)

  &:hover &__title
    color: $second
