html
    font-size: 62.5% //1 rem = 10px; 10px/16px = 62.5%
    font-family: 'General Sans', sans-serif
    scroll-behavior: smooth
    @include respond(xs)
        font-size: 50%
    &.non-scrollable
        overflow: hidden
body,
html
    width: 100%
    height: 100%
    margin: 0

body
    box-sizing: border-box
    background: $white
    font-size: 1.8rem
main
    flex: 1 1 auto
    &.loading
        opacity: 0
        animation: fade-up .6s forwards


*, *::after, *::before
    margin: 0
    padding: 0
    box-sizing: inherit
    font-family: inherit
    color: inherit

img, svg
    display: inline-block
    width: 100%

picture
    display: block
    source
        display: none
    //&, img
    //    min-height: 100%

::selection
    background: darken($second, 10%)
    color: $white

a
    display: block
    transition: all .3s

.wrapper
    display: flex
    flex-direction: column
    min-height: 100%
    overflow: clip