.airport-grid
  display: grid
  grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr))
  gap: 3rem 2rem
  // .airport-grid__item
  &__item
    position: relative
    display: flex
    flex-direction: column
    gap: 2rem
    &:hover
      .airport-grid__image
        img
          transform: scale(1.15)

  // .airport-grid__image
  &__image
    display: block
    position: relative
    padding-bottom: prc(217, 327)
    border-radius: .8rem
    overflow: hidden

    img
      position: absolute
      width: 100%
      height: 100%
      top: 0
      left: 0
      object-fit: cover
      transition: all .6s ease-in-out

  // .airport-grid__icon
  &__icon
    display: flex
    justify-content: center
    width: 100%
    height: 4rem

    img
      max-width: 12rem
      object-fit: contain