//--- Containers

.container, .container-sm,
.container-xs
  padding: 0 3rem
  margin: 0 auto
  @include respond(sm)
    max-width: 68rem
  @include respond(xs)
    max-width: 100%

.container
  max-width: 123rem

.container-sm
  max-width: 100rem

.container-xs
  max-width: 85rem

//--- End


//--- Sections

section
  scroll-margin-top: 10.5rem

.space-pd-v
  padding: 14rem 0
  @include respond(sm)
    padding: 12rem 0
  @include respond(sm)
    padding: 9rem 0

.space-pd-v-sm
  padding: 10rem 0
  @include respond(sm)
    padding: 8rem 0
  @include respond(sm)
    padding: 6rem 0

.space-pd-t
  padding-top: 14rem
  @include respond(sm)
    padding-top: 12rem
  @include respond(sm)
    padding-top: 9rem

.space-pd-t-sm
  padding-top: 10rem
  @include respond(sm)
    padding-top: 8rem
  @include respond(sm)
    padding-top: 6rem

.space-pd-b
  padding-bottom: 14rem
  @include respond(sm)
    padding-bottom: 12rem
  @include respond(sm)
    padding-bottom: 9rem

.space-mg-v
  margin: 14rem 0
  @include respond(sm)
    margin: 12rem 0
  @include respond(sm)
    margin: 9rem 0

.space-mg-v-sm
  margin: 10rem 0
  @include respond(sm)
    margin: 8rem 0
  @include respond(sm)
    margin: 6rem 0

.space-mg-t
  margin-top: 14rem
  @include respond(sm)
    margin-top: 12rem
  @include respond(sm)
    margin-top: 9rem

.space-mg-t-sm
  margin-top: 10rem
  @include respond(sm)
    margin-top: 8rem
  @include respond(sm)
    margin-top: 6rem

.space-mg-b-sm
  margin-bottom: 10rem
  @include respond(sm)
    margin-bottom: 8rem
  @include respond(sm)
    margin-bottom: 6rem

.space-mg-b
  margin-bottom: 14rem
  @include respond(sm)
    margin-bottom: 12rem
  @include respond(sm)
    margin-bottom: 9rem

.space-pd-h
  padding: 0 5rem
  @include respond(sm)
    padding: 0


//--- End


//--- Grids

.row-main
  display: flex
  flex-wrap: wrap
  margin: -1.5rem
  @include respond(xs)
    margin: -.75rem

//--- End


//--- Columns

[class*="col-"]
  padding: 1.5rem
  @include respond(xs)
    padding: .75rem

@for $i from 1 through 11
  $amount: $i * 100%
  $width: calc(#{$amount}/12)
  .col-#{$i}-12
    width: $width
  .col-md-#{$i}-12
    @include respond(md)
      width: $width
  .col-sm-#{$i}-12
    @include respond(sm)
      width: $width
  .col-xs-#{$i}-12
    @include respond(xs)
      width: $width

.col-full
  width: 100%

.col-md-full
  @include respond(md)
    width: 100%

.col-sm-full
  @include respond(sm)
    width: 100%

.col-xs-full
  @include respond(xs)
    width: 100%

//--- End