.img-grid
  display: flex
  flex-wrap: wrap
  $gap: 2rem
  gap: $gap

  &:hover &__item:not(:hover) img
    opacity: .7

  &__item
    width: calc(100%/3 - $gap)
    padding-bottom: 25%
    position: relative
    overflow: hidden
    border-radius: 1.5rem
    background: $dark
    transition: all .6s
    backface-visibility: hidden
    @include respond(sm)
      width: calc(50% - $gap)
      padding-bottom: 33.33%
    @include respond(xs)
      width: 100%
      padding-bottom: 75%
    img
      @include coverImage
      transition: inherit
    &:hover img
      transform: scale(1.1)
      opacity: 1