.picture
  border-radius: 2rem
  overflow: hidden
  position: relative
  padding-bottom: 65%
  margin-right: 1.2rem
  margin-bottom: 1.2rem
  filter: drop-shadow(1.2rem 1.2rem 0 lighten($prime, 30%))
  //filter: drop-shadow(1.6rem 1.6rem 0 $yellow-l1)
  img
    @include coverImage
    display: block

  &:before
    content: ''
    width: 100%
    height: 100%
    @include posAbsZero
    z-index: 1
    display: none

  &--prime, &--second
    &:before
      display: block
      mix-blend-mode: multiply

  //&--prime
  //  filter: drop-shadow(1.6rem 1.6rem 0 $prime)
  //  &:before
  //    background: rgba($prime, .2)
  //
  //&--second
  //  filter: drop-shadow(1.6rem 1.6rem 0 $yellow-l1)
  //  &:before
  //    background: rgba($yellow, .2)

.picture-box

  &--two, &--three, &--four
    gap: 2rem
    @include respond(xs)
      gap: 1.5rem

  &--two
    display: flex
    picture
      width: 50%

  &--three, &--four
    display: grid
    grid-template-columns: repeat(2, calc(50% - 1.5rem))
    grid-template-rows: repeat(2, calc(50% - 1.5rem))

  &--three picture:first-child
    grid-area: 1 / 1 / -1 / 2