.hero-home2
  text-align: center
  background: linear-gradient(to bottom, rgba($white, .85) 50%, $white 95%), url("../img/home/home-hero2.jpg") center/cover
  margin-top: 8rem

  &__title
    text-transform: capitalize

  &__text
    margin: 3rem 0 5rem

  &__form
    max-width: 70rem
    margin: 0 auto
    .inline-labels__button
      width: 40%
      @include respond(xs)
        width: auto
