.list-inline
  align-items: center
  margin: -.8rem
  li
    padding: .8rem
    width: 25%
  @include respond(sm)
    margin: -.5rem
    li
      padding: .5rem
  @include respond(xs)
    li
      width: 50%

.list-inline, .list-main
  display: flex
  flex-wrap: wrap
  li
    @include respond(sm)
      font-size: 1.4rem
    @include respond(xs)
      font-size: 1.6rem

.list-main
  display: flex
  flex-direction: column
  li, li a
    color: $dark
  li:nth-child(n+2)
    margin-top: 2rem

.list-main-inline
  display: flex
  flex-wrap: wrap
  margin: -1.6rem
  .list-main
    padding: 1.6rem
    width: 25%
  @include respond(xs)
    .list-main
      width: 50%

.list-ico li
  position: relative
  padding-left: 2.5rem
  i
    margin: 4px 1rem 0 0
    color: $green
    display: inline-block
    @include posAbsTL(-1px, 0)
  b
    display: inline-block
    margin-right: .5rem
    font-weight: 500
  &:nth-child(n+2)
    margin-top: 1.6rem

.list-breadcrumbs
  flex-wrap: wrap
  gap: .3rem
  &:first-child
    margin-bottom: 6rem
    @include respond(sm)
      margin-top: 2rem
    @include respond(xs)
      margin-bottom: 4rem
  &, li
    display: flex
    align-items: center
  li
    &, &:before
      position: relative
    &:nth-child(n+2):before
      content: '/'
      top: -2px
      margin: 0 .7rem 0 .4rem
    &.active a
      font-weight: 500
    a
      max-width: 25rem
      overflow: hidden
      white-space: nowrap
      text-overflow: ellipsis
      font-size: 1.6rem

.list-tags
  display: flex
  flex-wrap: wrap
  align-items: center
  gap: 1rem
  li
    @include respond(xs)
      flex-grow: 1
    a
      background: rgba($grey, .1)
      font-size: 1.2rem
      padding: .75rem 1.5rem
      border-radius: 1rem
      font-weight: 600
      text-align: center
      &:hover
        transform: translateY(-3px)
        box-shadow: $shadow2

    &.list-tags__airports a
      background: $dark
      color: $white
      &:hover
        background: $black

    &.list-tags__in-flight a
      background: $prime
      color: $dark
      &:hover
        background: lighten($prime, 10%)

    &.list-tags__travel a
      background: $second
      color: $white
      &:hover
        background: lighten($second, 10%)

.list-nav
  display: flex
  justify-content: center
  gap: 3rem
  padding: 1rem 0
  font-weight: 600
  transition: all .2s
  z-index: 3
  border-top: 1px solid transparent
  border-bottom: 1px solid transparent
  background: $white
  li a
    border-bottom: 1px solid $dark
    &:hover
      border-color: $second
  &.sticky
    position: fixed
    left: 0
    width: 100%
    border-color: rgba($grey-l1, .5)
    background: lighten($prime, 45%)
    box-shadow: $shadow1
    li a
      border-color: transparent