.card-cta
  padding: 2rem
  border-radius: 1.5rem
  transition: all .6s, width 1ms
  $this: &
  color: $black
  filter: drop-shadow(.6rem .6rem 0 $grey-l1)
  background: $light1

  &__title
    font-weight: 500

  &__form
    margin-top: 2rem
    input
      border-color: $grey-l1

  &.sticky
    position: fixed
    top: 11rem

  @include respond(sm)
    margin-top: 1.7rem
    &, &.sticky
      @include posBack
      width: 100%!important