.question-cta
  display: flex
  align-items: center
  flex-wrap: wrap
  width: 100%
  padding: 1.5rem 0
  @include posFixBL(0, 0)
  background: $light1
  justify-content: center
  gap: 2rem
  box-shadow: $shadow3
  z-index: 2
  opacity: 0
  transform: translateY(100%)
  transition: all .5s
  border-top: 1px solid rgba($grey-l1, .3)
  @include respond(sm)
    gap: 1rem
    padding: 1rem

  &.active
    opacity: 1
    transform: translateY(0)