.card-blog
  padding: 2rem
  border-radius: 1.5rem
  background: $light1
  transition: all .3s
  //filter: drop-shadow(1.6rem 1.6rem 0 lighten($prime, 30%))
  position: relative
  overflow: hidden
  color: $black
  animation: fade-down .5s forwards
  //filter: drop-shadow(.6rem .6rem 0 $grey-l1)
  &:hover
    box-shadow: $shadow1
  &:before
    content: '\201C'
    font-size: 45rem
    @include posAbsTR(-18rem, 0)
    z-index: 1
    color: rgba($grey-l1, .25)

  &__title, &__footer
    position: relative
    z-index: 2

  &__title a
    font-weight: 500

  &__footer
    margin-top: 1.5rem
    padding-top: 1.5rem
    border-top: 1px solid rgba($grey, .1)
    display: flex
    align-items: flex-end
    gap: 2rem
    @include respond(xs)
      flex-wrap: wrap

  &__tags
    flex-grow: 1

  &__date
    font-style: italic
    flex-shrink: 0
    @include respond(xs)
      flex-grow: 1
      display: flex
      justify-content: flex-end


.cards-blog
  position: relative
  &:before
    content: ''
    width: 4px
    height: 100%
    @include posAbsTL(0, 50%)
    background: $light1

  .card-blog
    &:nth-child(n+2)
      margin-top: 4rem
    &:nth-child(n+7)
      display: none

  &.active .card-blog:nth-child(n+7)
    display: block