.accordion

  &__item
    //&:nth-child(n+2)
    //  margin-top: 1rem

  &__title
    padding: 1.5rem 0
    border-bottom: 1px solid rgba($grey-l1, .4)
    cursor: pointer
    transition: color .6s
    font-weight: 600
    position: relative

    &:hover, &.active
      color: $second

    &:after
      content: '\25bc'
      @include posAbsTR(50%, 2rem)
      transform: translateY(-50%)
      transition: all .4s
      font-size: 1.5rem
      opacity: .3

    &.active
      font-weight: 700
      &:after
        transform: rotate(180deg) translateY(50%)
        color: $second
        opacity: 1
      & + .accordion__body
        max-height: 70rem
        padding: 1.5rem 0
        @include respond(sm)
          max-height: 100rem

  &__body
    padding-top: 0
    align-items: center
    gap: 3rem
    display: flex
    max-height: 0
    overflow: hidden
    transition: padding .3s, max-height .6s
    @include respond(sm)
      flex-wrap: wrap
    @include respond(xs)
      gap: 2rem

  &__img
    width: 35%
    flex-shrink: 0
    @include respond(sm)
      width: 70%
    @include respond(xs)
      width: 100%