.article
  display: grid
  grid-template-columns: calc(400%/12) calc(100%/12 - 3rem) 1fr
  grid-template-rows: repeat(2, min-content)
  gap: 1.5rem
  @include respond(md)
    grid-template-columns: calc(400%/12) 1fr
  @include respond(sm)
    grid-template-columns: repeat(2, 1fr)
    grid-template-rows: repeat(3, min-content)
    gap: 3rem 0

  &__list
    grid-area: 1 / 1 / 2 / 2
    @include respond(sm)
      grid-column: 1 / -1
      grid-row: 1 / 2

  &__cta
    grid-area: 2 / 1 / -1 / 2
    align-self: end
    @include respond(sm)
      grid-area: 3 / 1 / -1 / 4

  &__content
    grid-area: 1 / 3 / -1 / 4
    @include respond(sm)
      grid-column: 1 / -1
      grid-row: 2 / 3
    &__item
      animation: fade-up-big .4s forwards
      &:not(:first-child)
        display: none

.article-inline
  $this: &
  padding: 2rem
  border-radius: 1.5rem
  border: 1px solid rgba($grey-l1, .4)

  &__img
    @include respond(sm)
      margin-bottom: 3.4rem
      padding-right: 0

  &__content
    order: -1

  &:nth-child(n+2)
    margin-top: 3rem
    @include respond(sm)
      margin-top: 3.2rem