.inline-labels
  display: flex
  gap: 1rem
  border-radius: 1.5rem
  position: relative
  //box-shadow: $shadow1
  transition: all .5s
  height: 7rem
  $this: &
  @include respond(xs)
    flex-wrap: wrap
    height: auto

  //&:has(input:focus)
  //  box-shadow: $shadow2
  &:has(.btn--dark)
    input, textarea
      border-color: $black
  &:has(.btn--prime)
    input, textarea
      border-color: $prime
  &:has(.btn--second)
    input, textarea
      border-color: $second
  &:has(.btn--red)
    input, textarea
      border-color: rgba($dark, .5)

  &__input, &__button, &__trigger
    &, & > *
      height: 100%

  &__input, &__button
    @include respond(xs)
      width: 100%

  &__input
    flex-grow: 1

  &__button
    flex-shrink: 0
    .btn
      border-radius: 1.5rem
      padding: 1.6rem 2rem
      &:hover, &:focus
        transform: none
      @include respond(xs)
        font-size: 1.6rem
        i
          display: none

  &--animated
    max-width: 34rem
    transition: all 1s
    overflow: hidden
    border-radius: 1.5rem

    & > *
      width: 100%

    #{$this}

      &__input
        border-radius: 1.5rem

      &__button
        display: none

      &__trigger
        @include posAbsTL(0, 0)

    &.active
      width: 100%
      overflow: visible
      max-width: 100%
      & > *
        width: auto

      #{$this}

        &__trigger
          display: none

        &__button
          display: inline-flex