.footer
  background: $light1
  padding: 3rem 0
  border-top: 1px solid rgba($grey-l1, .5)
  a
    display: inline-block
  @include respond(xs)
    padding: 6rem 0

  &__row
    display: flex
    align-items: center
    @include respond(xs)
      flex-wrap: wrap

  &__logo
    flex-grow: 1
    @include respond(xs)
      width: 100%
      display: flex
      justify-content: center

  &__logo a
    max-width: 8rem
    max-height: 4rem
    transition: all .4s ease
    backface-visibility: hidden
    img
      width: 100%
      display: block

  &__links, &__inline-links
    display: flex
    align-items: center

  &__links
    flex-wrap: wrap
    gap: 2rem
    li
      width: calc(50% - 2rem)
    @include respond(xs)
      margin: 3rem 0
      li
        width: 100%

  &__inline-links
    gap: 4rem
    @include respond(sm)
      justify-content: center
    @include respond(xs)
      width: 100%
      margin-top: 5.6rem
      flex-wrap: wrap
      text-align: center
      gap: 2rem
      li
        width: 100%
        a
          display: inline-block

  &__right
    a:nth-child(n+2)
      margin-top: 2rem

  &--second *
    @include respond(xs)
      text-align: center
