.logos
  margin: -2rem
  @include respond(xs)
    flex-wrap: wrap
    margin: -1rem

  &, &__item
    display: flex
    align-items: center
    justify-content: center

  &__item
    padding: 2rem
    width: 25%
    @include respond(xs)
      padding: 1rem
    img
      max-height: 7rem
      display: block
      width: 100%
      max-width: 100%

.logos2
  display: grid
  align-items: center
  flex-wrap: wrap
  gap: 2rem 1rem
  grid-template-columns: repeat(7, 1fr)
  grid-template-rows: repeat(6, min-content)
  @include respond(sm)
    gap: 2rem
    grid-template-columns: repeat(4, 1fr)
  @include respond(xs)
    grid-template-columns: repeat(3, 1fr)
    grid-template-rows: repeat(9, min-content)


  &__item
    display: flex
    align-items: center
    justify-content: center
    img
      max-width: 8rem
      max-height: 5rem
      width: auto
      height: auto
      @include respond(xs)
        max-width: 10rem
