.section-image
  display: flex
  @include respond(xs)
    flex-wrap: wrap

  &__img
    position: relative
    width: 50%
    min-height: 100%
    flex-shrink: 0
    img
      @include coverImage
    @include respond(xs)
      width: 100%
      img
        @include posBack

  &__content
    padding: 3rem
    background: $light1
    @include respond(sm)
      padding: 2rem
    &.content p
      font-size: 1.6rem
      text-align: justify

.sections-image
  overflow: hidden
  border-radius: 1.5rem

  .section-image
    &:nth-child(even) .section-image__content
      order: -1
      @include respond(xs)
        order: 0