.popup
  @include posFixZero
  background: rgba(lighten($second, 20%), .8)
  z-index: 3
  animation: fade-up 1s
  backdrop-filter: blur(15px)
  padding: 3rem
  display: none

  &.active
    display: flex

  &, &__card
    align-items: center
    justify-content: center

  &__card
    display: flex
    width: 50%
    max-width: 100rem
    background: $white
    box-shadow: $shadow2
    overflow: hidden
    border-radius: 2.7rem
    position: relative
    text-align: center
    padding: 10vh 10vw
    overflow-y: auto
    @include respond(sm)
      width: 90%
      padding: 5vh 5vw
    @include respond(xs)
      width: 100%
    &::-webkit-scrollbar
      display: none
    &::-webkit-scrollbar-track
      background: #f1f1f1
    &::-webkit-scrollbar-thumb
      background: #888
    &::-webkit-scrollbar-thumb:hover
      background: #555

  &__close
    font-size: 2.4rem
    line-height: 2rem
    cursor: pointer
    transition: all .3s
    color: $grey-l2
    &:hover
      color: $second
    @include posAbsTR(2.4rem, 2.4rem)

  &__body
    //max-width: 27rem

  &__img
    width: 5.5rem

  &__title
    margin: 4rem 0 1rem
    @include respond(xs)
      margin: 3rem 0 1rem

  &__text span
    display: none
    &.active
      display: block

  &__cta
    margin-top: 4rem
    @include respond(sm)
      margin-top: 3rem