.form

  label, input
    display: block

  label
    position: relative
    &.has-error input
      border-color: $red

  span.error-message
    @include posAbsTL(calc(100% + .4rem), 2.4rem)
    font-size: 1.2rem
    font-weight: 600
    transition: all .3s
    transform: translateY(-1rem)
    opacity: 0

  input, textarea, button
    outline: none
    box-shadow: none
    border: none
    transition: all .4s
    -webkit-appearance: none
    width: 100%

    &::-webkit-search-decoration,
    &::-webkit-search-cancel-button,
    &::-webkit-search-results-button,
    &::-webkit-search-results-decoration
      display: none

  input, textarea
    color: $dark
    background: $white
    border: 1px solid $grey-l1
    font-weight: 600
    font-size: 1.6rem
    padding: 0 2rem
    border-radius: 1.5rem
    //&:focus, &:active
    //  border-color: $second
    &::placeholder
      color: rgba($dark, .7)
      font-weight: 500

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button
      -webkit-appearance: none
      margin: 0
    &[type=number]
      -moz-appearance: textfield

    &:active, &:focus
      border-color: $second!important

  textarea
    resize: none
    height: 20rem
    padding-top: 1.5rem
    padding-bottom: 1.5rem

  input
    min-height: 5.4rem
    color: $dark
    &:focus:invalid:not(:placeholder-shown)
      border-color: $red
      & + span
        transform: translateY(0)
        opacity: 1
        display: block

  &__label:nth-child(n+2)
    margin-top: 2rem

  &__cta
    margin-top: 2.4rem