.item-stat, .item-stat-out
  border-radius: 1.5rem
  display: flex

.item-stat
  background: $light1
  padding: 3rem 2rem
  min-height: 100%
  align-items: center
  justify-content: center
  text-align: center
  //box-shadow: $shadow2
  @include respond(xs)
    padding: 4rem 1.5rem

  &__amount
    color: $dark

  &__text
    margin-top: .8rem

.item-stat-out
  border-radius: 1.5rem
  flex-direction: column
  border: 1px solid rgba($grey-l1, .5)
  padding: 2rem
  //box-shadow: $shadow2
  b
    font-size: 3.5rem
    color: $second
    @include respond(sm)
      font-size: 3rem
  p
    margin-top: .5rem
    color: rgba($dark, .7)

.stats-out
  $gap: 2rem
  gap: $gap
  display: grid
  grid-template-columns: repeat(4, 1fr)
  grid-template-rows: repeat(2, min-content)
  &:after
    content: ''
    flex: auto

  @include respond(md)
    grid-template-columns: repeat(3, auto)
    grid-template-rows: repeat(3, min-content)
  @include respond(sm)
    grid-template-columns: repeat(2, auto)
    grid-template-rows: repeat(4, min-content)
    gap: 1rem

  .item-stat-out
    &:nth-child(5)
      grid-area: 2/1/3/3
    @include respond(md)
      &:nth-child(7)
        grid-area: 3/1/-1/3
    @include respond(sm)
      &:nth-child(n+1)
        grid-area: auto
      &:nth-child(5)
        grid-column: 1 / -1

.stats
  display: flex
  flex-wrap: wrap
  $gap: 2rem
  gap: $gap
  .item-stat
    width: calc(100%/3 - $gap/1.5)
    @include respond(sm)
      width: calc(50% - $gap)
      &:last-child
        width: 100%