[class*='text-']
  font-weight: 400
  line-height: 1.5
  @include respond(md)
    line-height: 1.6

.text-lg
  font-size: 2.6rem

.text-big, .text-link
  font-size: 1.8rem

.text-md
  font-size: 1.6rem

.text-sm
  font-size: 1.4rem

.text-link
  text-decoration: underline
  color: $blue-l1
  font-weight: 600