//--- Visibility

.hidden
  display: none

.block
  display: block

.hidden-md
  @include respond(md)
    display: none

.visible-md
  display: none
  @include respond(md)
    display: block

.hidden-sm
  @include respond(sm)
    display: none

.hidden-xs
  @include respond(xs)
    display: none

.visible-sm
  display: none
  @include respond(sm)
    display: block

.visible-xs
  display: none
  @include respond(xs)
    display: block

//--- End


//--- Alignment, orders, spaces, positions, borders, boxes

.divider
  border-bottom: 1px solid rgba($grey-l1, .6)
  margin: 6rem 0
  height: 0
  @include respond(xs)
    margin: 4rem 0

.pos-rel
  position: relative

.bd-t
  border-top: 1px solid rgba($grey-l1, .5)

.box, .box-bordered
  padding: 3rem
  border-radius: 1.5rem
  box-shadow: $shadow1

.box
  background: $light1

.box-bordered
  border: 1px solid rgba($grey-l1, .4)

//--- End


//--- Texts

.upper
  text-transform: uppercase
.bold
  font-weight: bold
.s-bold
  font-weight: 600
.md-bold
  font-weight: 500
.italic
  font-style: italic
.center
  text-align: center

//--- End


//--- Margins and Paddings

@for $i from 1 through 20
  $step: $i * 8
  $space: .8rem * $i
  .pd-t-#{$step}
    padding-top: $space
  .pd-b-#{$step}
    padding-bottom: $space
  .pd-l-#{$step}
    padding-left: $space
  .pd-r-#{$step}
    padding-right: $space
  .mg-t-#{$step}
    margin-top: $space
  .mg-b-#{$step}
    margin-bottom: $space
  .mg-l-#{$step}
    margin-left: $space
  .mg-r-#{$step}
    margin-right: $space

//--- End


