//--- Responsiveness

//- xxs: 576px
//- xs: 768px
//- sm: 992px
//- md: 1200px
//- lg: 1900p

// - 1em - 16px. Default in browsers

@mixin respond($resolution)

  @if $resolution == lg
    @media (max-width: 118.75em)
      @content

  @if $resolution == md
    @media (max-width: 85em)
      @content

  @if $resolution == sm
    @media (max-width: 62em)
      @content

  @if $resolution == xs
    @media (max-width: 41em)
      @content

@mixin respondCustom($resolution)
  @media (max-width: $resolution + px)
    @content

@mixin respondAboveXS
  @media (min-width: calc(41em + 1px))
    @content

@mixin respondAboveSM
  @media (min-width: calc(62em + 1px))
    @content

@mixin respondVertical($resolution)
  @media (max-height: $resolution + px)
    @content

//--- End


//--- Positions

@mixin posAbs($top, $right, $bottom, $left)
  position: absolute
  top: $top
  right: $right
  bottom: $bottom
  left: $left

@mixin posAbsZero
  @include posAbs(0, 0, 0, 0)

@mixin posAbsTR($top, $right)
  position: absolute
  top: $top
  right: $right

@mixin posAbsTL($top, $left)
  position: absolute
  top: $top
  left: $left

@mixin posAbsBR($bottom, $right)
  position: absolute
  bottom: $bottom
  right: $right

@mixin posAbsBL($bottom, $left)
  position: absolute
  bottom: $bottom
  left: $left

@mixin posFix($top, $right, $bottom, $left)
  position: fixed
  top: $top
  right: $right
  bottom: $bottom
  left: $left

@mixin posFixTL($top, $left)
  position: fixed
  top: $top
  left: $left

@mixin posFixTR($top, $right)
  position: fixed
  top: $top
  right: $right

@mixin posFixBL($bottom, $left)
  position: fixed
  bottom: $bottom
  left: $left

@mixin posFixBR($bottom, $right)
  position: fixed
  bottom: $bottom
  right: $right

@mixin posFixZero
  @include posFix(0, 0, 0, 0)

@mixin posBack
  position: relative!important
  top: 0!important
  right: 0!important
  bottom: 0!important
  left: 0!important
  transform: translate(0, 0)

//--- End


//--- Cover image

@mixin coverImage
  @include posAbsZero
  width: 100%
  height: 100%
  object-fit: cover

//--- End


